button {
  background-color: var(--theme-color);
  transition-duration: 0.4s;
  border: 2px solid var(--theme-color);
  border-radius: 25px;
  color: white;
  padding: 5px 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  overflow-wrap: initial;
}

.follow-action-btn {
  width: 6rem;
}

.log-out-btn {
  min-width: 6rem;
}
  
button:hover{
  background-color: white;
  color: var(--theme-color)
}

button:focus { 
  outline-style: none; 
}

.btn-container-right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.close-btn-bottom {
  border-bottom: var(--barker-border);
  height: 55px;
}

.arrow-left-container {
  height: 60px;
}

#arrow-left{
  height: 40px;
  width: 40px;
  margin: 10px;
  padding: 10px;
  border-radius: 50%;
  font-size: x-large;
}

#arrow-left:hover {
  color: var(--theme-color);
  background-color:var(--hover-background);
  cursor: pointer;
}

.icon {
  margin: 10px;
  border-radius: 25px;
  color: var(--icon-color);
}

.icon:hover, .footer-icon:hover {
  cursor: pointer;
  color: var(--theme-color); 
}

.icon-close {
  font-size: x-large;
}

.active {
  color: var(--theme-color);
}


